<template>
  <div class="formItem">
    <el-form label-width="100px" :model="ProductDetails">
      <el-form-item label="商品名称:">{{ProductDetails.GoodsName}}</el-form-item>
      <!-- <el-form-item label="商品条码:">{{ProductDetails.SerialNumber}}</el-form-item> -->
      <el-form-item label="商品类型:">{{ProductDetails.GoodsGroupsName}}</el-form-item>
      <el-form-item label="商品单位:">{{ProductDetails.Specifications}}</el-form-item>
      <el-form-item label="商品保质期:">{{ProductDetails.ShelfLife}}天</el-form-item>
      <!-- <el-form-item label="建议零售价:">{{ProductDetails.OriginalPrice}}元</el-form-item> -->
      <el-form-item label="商品供货价:">{{ProductDetails.supplierPrice}}元</el-form-item>
      <el-form-item label="装箱数:">{{ProductDetails.PackingNumber}}</el-form-item>
      <el-form-item label="商品描述:" class="showImg">{{ProductDetails.Describe}}</el-form-item>
      <el-form-item label="商品属性:" class="showImg">
        <el-card>
          <div v-for="(items,index) in GoodsValueMappingList" :key="index">
            {{items.CategoryType | CategoryType}}:
            <div v-for="(item,ind) in items.GoodsValueList" :key='ind' style="padding-left:10px;">
              {{item.GoodsValueName}}; <span style="color:red;">条码：</span>{{item.SerialNumber}}
            </div>
          </div>
        </el-card>
      </el-form-item>
      <el-form-item label="商品展示图:" class="showImg">
        <el-image style="width:200px; height: 200px" :src="ProductDetails.Image" fit="scale-down"></el-image>
      </el-form-item>
      <el-form-item label="商品详情图:" class="showImg">
        <el-image
          style="width:200px; height:200px"
          :src="item.Src"
          fit="scale-down"
          v-for="(item,index) in ProductDetails.ImgList"
          :key="index"
        ></el-image>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { GetGoodDetailedAync,GetGoodsGroupList } from "@/api/product";
export default {
  name: "ProductDetails",
  data() {
    return {
      ProductDetails:{},
      GoodsValueMappingList:[
         {
      CategoryType: 1, //口味
      GoodsValueList: [
      ]
    },
    {
      CategoryType: 2, //颜色
      GoodsValueList: [
      ]
    },
    {
      CategoryType: 3, //尺寸
      GoodsValueList: [
      ]
    }
      ]
    };
  },
 created() {
    // this.$request.GetGoodDetailedAync(this.$route.query.id).then(res=> {
    //   let response=res.content;
    //   let ValuesList=response.ValuesList;
    //   for(let i=0;i<ValuesList.length;i++){
    //     let {CategoryType,GoodsValueName,SerialNumber}=ValuesList[i];
    //        this.GoodsValueMappingList[CategoryType-1].GoodsValueList.push({GoodsValueName,SerialNumber})
    //   }
    //    this.GoodsGroupList(response);
    // })
    this.GetGoodDetaile(this.$route.query.id);
  },
  methods: {
   async GetGoodDetaile(GoodsID){
      let res= await this.$request.GetGoodDetailedAync(GoodsID);//根据商品id获取详情
      let response=res.content;
      let ValuesList=response.ValuesList;
      for(let i=0;i<ValuesList.length;i++){//重构属性排序
        let {CategoryType,GoodsValueName,SerialNumber}=ValuesList[i];
           this.GoodsValueMappingList[CategoryType-1].GoodsValueList.push({GoodsValueName,SerialNumber})
      }
      let GroupList= await this.GoodsGroupList(response);//获取商品类型

      let SuppliersID=this.$store.getters.userInfoS.SuppliersID;
      let supplierPrice=await this.$request.GetSupplierGoodsPric(GoodsID,SuppliersID);
      let Price=supplierPrice.content[0].Price;
      GroupList.supplierPrice=Price;
      this.ProductDetails =GroupList;
    },
   async GoodsGroupList(response) {
      var self=this;
      var id=response.GoodsGroups_ID;
     let resp=await this.$request.GetGoodsGroupList("");//获取全部商品类型
     let res=resp.content;
        let obj={};
        for(let i=0;i<res.length;i++){
            if(res[i].ID==id){
              obj=res[i];
              break;
            }
        }
        let DataList = [];
        if(obj.ParentID==0){
            DataList=[{Name:""}]
        }else{
            DataList=res.filter(x => {//过滤出与商品id相等的等级1的值
              return x.Level == 1&&x.ID==obj.ParentID
        });
        }
        response.GoodsGroupsName=DataList[0].Name+"/"+obj.Name;
        return response;
    }
  },
  filters: {
    CategoryType(value) {
      if (value == 1) {
        return "口味";
      } else if (value == 2) {
        return "颜色";
      } else if (value == 3) {
        return "尺寸";
      }
    }
  }
};
</script>
<style scoped>
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
.formItem{
  max-width:800px;
  padding:15px;
  margin:0 auto;
}
.formItem form>div.el-form-item.showImg::before{
  content: "";
  display: block;
  clear: both;
}
.formItem form>div.el-form-item:not(.showImg){
  width:50%;
  float:left;
}
</style>



