//获取所有的商品分类（Get）:参数：KeyWords ：查询关键字
export async function GetGoodsGroupList(KeyWords){
  let query={
    KeyWords:KeyWords
  }
  return await get("Goods/GetGoodsGroupList",query)
}

//获取商品详细（Get）参数：GoodsID：商品ID
export async function GetGoodDetailedAync(GoodsID){
  let query={
    GoodsID:GoodsID
  }
  return await get("Goods/GetGoodDetailedAync",query)
}
//商品下架（Post）参数：GoodsID：商品ID
export async function UnShelvesGoods(GoodsID){
  let query={
    GoodsID:GoodsID
  }
  return await post("Goods/UnShelvesGoods",query,query)
}
